import { Fetch } from "../func/simples.js";
const nvlang = (navigator.language || navigator.userLanguage).split("-")[0];

export const lgn = localStorage.getItem("lang:") ?? nvlang;

export async function Fetch_lang() {
  try {
    return await Fetch("lang/" + lgn + "_lang.json");
  } catch {
    console.log('"lang/' + lgn + '_lang.json"' + " no exist");
  }
}
export function change_lang(lang) {
  localStorage.setItem("lang:", lang);
}
export function default_lang() {
  localStorage.setItem("lang:", "en");
}
export function auto_lang() {
  localStorage.removeItem("lang:");
}
