import { Fetch, splitpoint } from "./func/simples.js";
import { uFetch } from "./href.js";
import { name_search } from "./func/search.js";
import { legacy_read } from "./analyze/legacy.js";
import { analyze_content } from "./analyze/content.js";

export async function readdefs(nm) {
  nm = splitpoint(nm);
  let nms = (await uFetch("nm"))[nm[0]];
  nm = await readfiles(nms, nm);
  return await nm;
}
async function verify_file(nm) {
  nm = await nm;
  if (nm.legacy) {
    nm.obj = await legacy_read(nm.obj, await readdefs(nm.legacy_name));
  }
  if (nm.obj.content) {
    nm.obj.content = await analyze_content(nm.obj.content);
  }
  return nm;
}
async function readfiles(url, nm) {
  const file = await Fetch(url);
  nm = name_search(file, nm[1]);
  nm = verify_file(nm);
  return nm;
}

export function analyze_dynamic(obj, nm) {
  if (obj.content) {
    return name_search(obj.content, nm).obj;
  }
}
