import { uFetch } from "../href.js";

function $file_script(src, module) {
  let options = {
    type: module == !0 ? "module" : "",
    src,
    defer: true,
  };
  document.head.append(
    Object.assign(document.createElement("script"), options)
  );
}
async function $loadscript() {
  (await uFetch("script")).map((e) => {
    setTimeout(() => $file_script(e.url, e.module), 1500);
  });
}
export async function $script() {
  $loadscript();
}
