import { inc } from "./simples.js";

export function name_search(obj, search) {
  const legacy = Object.keys(obj).find((a) => a.startsWith(search + "@"));
  const bs = {};
  if (obj[search]) {
    bs["legacy"] = !1;
    bs["obj"] = obj[search];
  } else if (legacy) {
    bs["legacy"] = !0;
    bs["legacy_name"] = legacy.split("@")[1];
    bs["obj"] = obj[legacy];
  }
  return bs;
}
