import { tohtml } from "./func/tohtml.js";
import { readdefs, analyze_dynamic } from "./analyze.js";

export const appd = (obj, apd) =>
  document.querySelector(apd).append(tohtml(obj));

export async function set_ui(nm, sub) {
  nm = (await readdefs(nm)).obj;
  return nm;
}
export class dynamic_ui {
  constructor(elem, nm) {
    this.elem = analyze_dynamic(elem, nm);
  }
  onid(a) {
    this.elem.id = a;
  }
  onlang(a) {
    this.elem.onlang = a;
  }
  text(a) {
    this.elem.text = a;
  }
  src(a) {
    this.elem.src = a;
  }
  href(a) {
    this.elem.href = a;
  }
  type(a) {
    this.elem.type = a;
  }
  onclass(a) {
    if (this.elem.class) {
      a.forEach((e) => {
        this.elem.class.push(e);
      });
    } else {
      this.elem.class = [];
      a.forEach((e) => {
        this.elem.class.push(e);
      });
    }
  }
}
export async function add_ui(nm, apd) {
  appd((await readdefs(nm)).obj, apd);
}
