import { Fetch } from "./func/simples.js";
let config = "ui_config.json";
export async function uFetch(type) {
  if (type == "css") {
    return (await Fetch(config)).dom_config.css;
  } else if (type == "script") {
    return (await Fetch(config)).dom_config.script;
  } else if (type == "font") {
    return (await Fetch(config)).dom_config.font;
  } else if (type == "init") {
    return (await Fetch(config)).ui_config.init;
  } else if (type == "nm") {
    return (await Fetch(config)).ui_config.namespace;
  }
}
