import { inc } from "../func/simples.js";
import { name_search } from "../func/search.js";
import { legacy_read } from "./legacy.js";
import { readdefs } from "../analyze.js";

const nm_content = (x) => (inc(x, "@") ? x.split("@")[0] : x);

export async function analyze_content(analyze) {
  for (const x in analyze) {
    let obj = name_search(analyze, nm_content(x));
    if (obj.legacy) {
      obj.obj = await legacy_read(obj.obj, await readdefs(obj.legacy_name));
    }

    if (analyze[x].content) {
      analyze[x].content = await analyze_content(analyze[x].content);
    }
    analyze[x] = obj.obj;
  }
  return await analyze;
}
