import { uFetch } from "../href.js";
function $file_css(src) {
  let options = {
    rel: "stylesheet",
    href: src,
  };
  document.head.append(Object.assign(document.createElement("link"), options));
}
const $loadcss = async () =>
  (await uFetch("css")).map((file) => $file_css(file.url));

export async function $css() {
  $loadcss();
}
