import { uFetch } from "../href.js";
async function $file_font(e) {
  if (e.name && e.url) {
    let options = { family: e.name, url: "url(" + e.url + ")" };
    const fts = new FontFace(options.family, options.url);
    fts.load();
    document.fonts.add(fts);
  }
}
async function $loadfont() {
  (await uFetch("font")).map((e) => $file_font(e));
}
export async function $font() {
  $loadfont();
}
