const Fetch = async (u) => await (await fetch(u)).json();

const inc = (a, s) => a.includes(s);

const Id = (i) => document.getElementById(i);

function splitpoint(obj) {
  if (inc(obj, ".")) {
    return (obj = obj.split(".")), obj[0] && obj[1] ? obj : !1;
  }
}

export { inc, Fetch, Id, splitpoint };
